.form {
  padding: 10px 20px;
  width: 200px;
  background-color: #d2d1d1;
  font-family: "Gill Sans", sans-serif;
  border-radius: 5px;
}

.title{
    font-size: 16px;
    font-weight: normal;
    text-align: center;
    margin-bottom: 20px;
}

.fields{
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
  row-gap: 10px;
}
.select{
  padding: 10px;
}